import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { KycVerificationMethod, Store } from './store';
import { ToastController } from '@ionic/angular';

@Component({
  selector: 'app-kyc',
  templateUrl: './kyc.page.html',
  styleUrls: ['./kyc.page.scss'],
})
export class KycPage implements OnInit {
  cards: KycVerificationMethod[] = Store.verificationMethod;

  constructor(private router: Router, public toastCtrl: ToastController) {}

  ngOnInit() {}

  openLink(page: any) {
    if (page.link) {
      this.router.navigate([`${page.link}`]);
      localStorage.removeItem('kycRef');
      localStorage.removeItem('UploadedDocument');
    } else {
      this.showToast();
    }
  }

  goBack() {
    this.router.navigate(['dashboard']);
  }

  async showToast() {
    const toast = await this.toastCtrl.create({
      message: 'Service not Available',
      duration: 2000,
      position: 'bottom',
    });
    toast.present();
  }
}
